import React from 'react';
import Grid from "@material-ui/core/Grid";
import DevSearchBox from "../../components/common/DevSearchBox";
import KnowledgeTypeRadio from "./KnowledgeTypeRadio";
import KmStatusMe from "./KmStatusMe";
import SystemStatus from "./SystemStatus";

Index.propTypes = {};

function Index() {
    function searchGlobal() {

    }

    return (
        <Grid style={{marginBottom: 72}} container>
            <Grid xs={12} item>
                <DevSearchBox disabled={true} searchMethod={searchGlobal}/>
            </Grid>
            {/*<Grid xs={12} item>*/}
            {/*    <KnowledgeTypeRadio/>*/}
            {/*</Grid>*/}
            <Grid xs={12} item>
                <KmStatusMe userId={JSON.parse(localStorage.getItem("user")).userId}/>
            </Grid>
            <Grid xs={12} item>
                <SystemStatus/>
            </Grid>

        </Grid>
    );
}

export default (Index);