import React from 'react';
import "./assets/css/bootstrap.css";
import "./assets/css/customBS4.css";
import './App.css';
import {BrowserRouter as Router, Route} from "react-router-dom";
import Dashboard from "./components/Dashboard";
import SignIn from "./views/SignIn";
import {makeStyles} from "@material-ui/core/styles";
import {SnackbarProvider} from "notistack";
import "./assets/css/anim.css";
import {BackDropProvider} from "./config/BackDropContext"
import Root from "./components/Root";
import Home from "./views/Home";
import moment from "moment";
import jMoment from "moment-jalaali";
import {useAddToHomescreenPrompt} from "./components/common/useAddToHomescreenPrompt";
import DevDialog from "./components/common/DevDialog";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";


jMoment.loadPersian({dialect: "persian-modern", usePersianDigits: true});
moment.updateLocale('fa'
    , {
        jMonthsShort: ['فروردین', 'اردیبهشت', 'خرداد', 'تیر', 'مرداد', 'شهریور', 'مهر', 'آبان', 'آذر', 'دی', 'بهمن', 'اسفند'],
    }
);
const styles = makeStyles((theme) => ({
    success: {backgroundColor: 'purple'},
    error: {backgroundColor: 'blue'},
    warning: {backgroundColor: 'green'},
    info: {backgroundColor: 'yellow'},
}));
export default function App() {
    const [prompt, promptToInstall] = useAddToHomescreenPrompt();
    const [isVisible, setVisibleState] = React.useState(false);
    const hide = () => setVisibleState(true);
    const classes = styles();

    React.useEffect(
        () => {
            if (prompt) {
                setVisibleState(true);
            }
        },
        [prompt]
    );
    if (!isVisible) {
        return (
            <BackDropProvider>
                <SnackbarProvider
                    autoHideDuration={1500}
                    className="animate__bounceIn  animate__animated"
                    // variant={['default','error','success','warning','info','alarm']}
                    classes={{
                        // variantSuccess: classes.success,
                        // variantError: classes.error,
                        // variantWarning: classes.warning,
                        // variantInfo: classes.info,
                        variantAlarm: classes.alarm,
                    }}
                    maxSnack={5}>
                    <Router>
                        <Route path='/main' component={Root}/>
                        <Route path='/dashboard' component={Dashboard}/>
                        <Route path='/signIn' component={SignIn}/>
                        <Route exact path='/' component={Home}/>
                    </Router>
                </SnackbarProvider>
            </BackDropProvider>
        )
    } else {
        return (
            <DevDialog open={isVisible} handleClose={hide}>
                <Grid spacing={4} container>
                    <Grid xs={12} item>
                        برای ادامه کار نرم افزار را باید نصب نمایید
                    </Grid>
                    <Grid xs={4} item/>
                    <Grid xs={4} item>
                        <Button size={"small"} fullWidth variant={"contained"} onClick={promptToInstall}
                                color={"primary"}>نصب</Button>
                    </Grid>
                    <Grid xs={4} item/>

                </Grid>
            </DevDialog>
        )
    }
}

